import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
     <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/33e4e59586b8c9a7db7b9453ef5fbea6ff9c11d5ba08edbc81eb1ed058b13b1c.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">UI/UX Design</h1>
            <p className="header__lead">Even the most powerful, comprehensive, and reliable software solutions can fall short of success without an intuitive design and a pleasant user experience. Intellectsoft can help you walk that extra mile.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>While beautiful design is imperative to attracting users, good UX is about honing in on outcomes which provide value to your users and retain them. At Intellectsoft, we bring you both. Our seasoned team of designers and developers work hand-in-hand to bring you a finished product that is both easy on the eyes and seamlessly functional, resulting in a well-rounded digital user experience.</p>
          </div>
        </section>
        <CasestudySlider/>
        <Commonsections/>
        <Statistics/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>
    
     );
   }
 }



