import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
        <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/72303b5bc0b5116940bf4b83d86efe4484efb077876d3b1863b49da74cbf7550.jpg')+ ')'}}>
          <div className="container">
            <h1 className="header__title">Software Maintenance &amp; Support</h1>
            <p className="header__lead">Successful software development doesn’t end at delivery. Intellectsoft can offer your business a way to maintain, update, and support your software solutions, allowing you to adapt to changing market demands quickly and efficiently.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>Perfect software has never been built, and that's perfectly alright. User preferences vary too widely and change too rapidly for software to simultaneously satisfy everyone. The one thing all users do expect, however, is a baseline level of reliability and performance. That's why you'll want a clear plan of action for maintaining a consistent user experience — regardless of what technical challenges you face behind the scenes.</p>
          </div>
        </section>
        <CasestudySlider/>
        <Commonsections/>
        <Statistics/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>
     );
   }
 }



