import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
     <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/0812d7f7cbb51a7000592043c9c3826cd4042a10026127e5ebb61e0fdd9a3c9e.jpg')+ ')'}}>
          <div className="container">
            <h1 className="header__title">Enterprise Product Strategy</h1>
            <p className="header__lead">In today’s rapidly changing business landscape, your company can’t be held back by clumsy or antiquated software. Our deep bench of talented developers and project managers will analyze your challenges, diagnose success blockers, craft a comprehensive strategic product plan, and help you develop a fully-customized software solution designed to streamline efficiencies, cut costs, boost client engagement, and more. We can help you formulate a solid enterprise product strategy.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <h3>Big Data Management &amp; Processing</h3>
            <p>Regardless of size, every business relies on leveraging data to make prompt, educated decisions. Intellectsoft can help you gather, analyze, and visualize the data that your employees and key-decision makers will need. We can develop a solution that will provide you with a thorough, automated analysis of your data, making it available in a comprehensive, intuitive interface complete with dashboards that provide key information exactly when you need it. Intellectsoft can even take it a step further by leveraging the latest technological advances that enable the creation complex Data Mining and Predictive Analytics solutions.</p>
            <h3>Cloud Computing &amp; Workflow Automation</h3>
            <p>Increase the efficiency of your daily business operations with software solutions that automate workflow enable your employees to minimize or completely eliminate time spent on performing routine operations. Intellectsoft can help your enterprise create cloud computing solutions that provide flexibility, data recovery, collaboration, and security within the company.</p>
            <h3>Consulting</h3>
            <p>With over 9 years of experience in developing complex software solutions for enterprises, SMBs, and startups, Intellectsoft can help your business discover new opportunities for growth and develop comprehensive product strategies. Our consultants can conduct a thorough analysis of your business operations and the software that helps you run your day-to-day and provide you with a roadmap towards further growth.</p>
            <hr />
            <h2>Our Workflow</h2>
            <p>Discovery &amp; Analysis — A qualified team of system-analysts and consultants observes your current solutions, discovers key problems and opportunities for improvement, and develops a roadmap based on your business needs.</p>
            <p>Development &amp; Testing — Qualified developers with niche expertise develop, test, and deliver a turnkey solution for your enterprise.</p>
            <p>Implementation &amp; Support — Intellectsoft aids you in implementing the new software into your daily business operations and provides thorough support and maintenance of your software.</p>
            <hr />
            <h2>Technology</h2>
            <p>Web Development: Java, .NET, PHP, JavaScript</p>
            <p>Mobile Development: iOS, Android, Xamarin, Hybrid</p>
            <p>Database: MySQL, PostgreSQL, SQL Server, Oracle, MongoDB and others</p>
            <p>DevOps and Cloud: AWS, Azure, Docker, Jenkins</p>
          </div>
        </section>
         <CasestudySlider/>  
         <Commonsections/>  
         <Statistics/>  
         <SuccessStories/>  
         <AboutYourProject/>  
      </div>
     );
   }
 }



