import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
     <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/2728f6721d46760a96d09fbe2900082d8d407791b4f81963e8457a6be414ecee.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">Security</h1>
            <p className="header__lead">Your clients’ trust can only be as good as your software’s security. Intellectsoft offers thorough security assessments, software testing and QA services, as well as incident response and expertise in developing stronger, more reliable security for your software.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>The data and communication revolution comes with one downside: the always-on, always-connected world has a unique set of vulnerabilities.</p>
            <p>According to <a href="https://www.symantec.com/security-center/threat-report">Symantec’s 2016 Internet Security Threat Report</a>, nearly 75 percent of all legitimate websites have unpatched security vulnerabilities and over half a billion records were estimated to be lost due to unreported security breaches in 2015.</p>
            <p>Not a week goes by that we don’t hear about a new, massive data security breach. Our security specialists know how to prevent and mitigate the risk of cyberattacks and our development and builds are mindful of the need to keep your operations safe at all times.</p>
            <h4>We offer:</h4>
            <ul>
              <li>Application Security Assessments, whereby we analyze the state of your applications and systems to identify vulnerabilities, compare them to established competitors, and conduct threat modeling.</li>
              <li>Incident Forensics and Response, where we step in and respond to security incidents by performing a rapid forensic analysis of complex security data across the enterprise, performing damage control, tracing attacks to their route, and offering remediation recommendations.</li>
              <li>Security Evaluation and Penetration Testing, a proactive way of exploiting the vulnerabilities in a system, safely, to determine whether unauthorized access or other malicious activity is possible. We can identify which flaws pose a threat to the application and install preventative measures.</li>
              <li>Software Testing and Quality Control (QA), since flawed concepts and broken features can reveal themselves at any point along the path. In order to launch your best possible product, you'll need a reliable system of software testing and quality control to detect minor issues before they become major problems. Our team of software testing specialists collect methodical diagnostics on functionality, reliability, performance and maintainability to help you save on time-consuming and costly repairs after release.</li>
            </ul>
          </div>
        </section>
        <CasestudySlider/>
        <Commonsections/>
        <Statistics/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>    
     );
   }
 }



