import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import SuccessStories from './common-Sections/SuccessStories';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
     <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../assets/5be2fa1f/img/case-studies-bg.jpg')+ ')'}}>
        <div className="container">
          <h1 className="header__title">App Developers</h1>
          <p className="header__lead">Get started on your software development project quickly by hiring some of Eastern Europe’s most qualified mobile and cross-platform app developers. With experience in consumer-oriented and enterprise application development, our highly qualified team can help you build and deploy solutions that will give your business an arsenal of mobile tools catered to accomplishing key objectives.</p>
        </div>
      </section>
       <section className="about">
        <div className="container">
          <p>Intellectsoft employs over 250 app developers, UI/UX designers, and QA engineers—each with at least 5 years of development experience and multiple successful mobile app projects behind their back. Our hiring and onboarding process has been perfected over 10 years, giving you and your business the opportunity to hire full-cycle app development teams and scale them quickly should the need arise.</p>
          <hr />
          <h3>iOS App Developers</h3>
          <p>Our team of experienced iOS app developers can take your idea and turn it into a stellar application for iPhones and iPads (complete with Apple CarPlay and Apple Watch compatibility). Get in touch with us to learn more about our low-code mobile application platform and custom app development services.</p>
          <hr />
          <h3>Android App Developers</h3>
          <p>Expedite the hiring process for your Android application development project by outsourcing your development needs to Intellectsoft’s experienced Android app developers.</p>
          <hr />
          <h3>Cross Platform App Development</h3>
          <p>Kill two (or three) birds with one stone and build cross-platform applications quickly with the help of our highly qualified app developers. We can develop native iOS, Android, and Windows Phone applications that use the operating system’s native user interface and allow apps across multiple platforms to share the same code—significantly reducing development time and helping your business boost its mobile development ROI.</p>
        </div>
      </section>   
      <CasestudySlider/> 
      <Commonsections/> 
       <Statistics/>    
      <SuccessStories/>         
      <AboutYourProject/>         
      </div>
      );
   }
}



