import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
      <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/5f31dea72f3f306341c3c6862d1d25717d0d7daf682a2bbb2a5d505472fd2d4c.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">Market &amp; Product Research</h1>
            <p className="header__lead">Whether you are thinking about developing new solutions or are already in the thick of it—Intellectsoft can help you steer your product to a lucrative market niche.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>Intellectsoft can help shed light on your market positioning and refine your marketing approach through custom surveys and research and use the latest in business intelligence to drill down and deliver actionable reports.</p>
            <p>Whether you’re selling licensed products, SaaS, or mobile solutions, Intellectsoft’s quantitative product research will give you an edge on the competition by utilizing only the technology and methodologies most appropriate to solving your market challenges.</p>
            <p>For emerging companies we can estimate demand and market size; or established enterprises we integrate data from customer CRM to locate customer pain points. In both cases, our job is to ensure that you aren’t left relying on just your instincts and biases.</p>
            <p>By better understanding the needs of your customers and employees, you can respond faster to client needs, minimize wasted effort, and focus on what creates value for your users.</p>
          </div>
        </section>
          <CasestudySlider/>
          <Commonsections/>
          <Statistics/>
          <SuccessStories/>
          <AboutYourProject/>
      </div>
  
     );
   }
 }



