import React from 'react';

import Commonsections from './common-Sections/common-sections';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';

export default class Footer extends React.Component {
  render()
   {
     return (
  <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/5db200684e0e74301efbd54eed572352c468905d15e564178ab0d8c516981fd6.jpg')+ ')'}}>
          <div className="container">
            <h1 className="header__title">Product Development Services</h1>
            <p className="header__lead">Intellectsoft has nearly a decade of experience in providing extensive product services for all stages of product development, from product ideation to design and marketing. We help you enhance your product offering through strategic development, product positioning, targeted marketing, and an intuitively designed user experience.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <div className="block-features block-features--main">
              <div className="block-features__item block-features__item--link">
                <a href="product-strategy.html">
                  <h3 className="block-features__heading">Enterprise Product Strategy</h3>
                  <p className="block-features__text">In today’s rapidly changing business landscape, your company can’t be held back by clumsy or antiquated software. Our deep bench of talented developers and project managers will analyze your challenges, diagnose success blockers, craft a comprehensive strategic product plan, and help you develop a fully-customized software solution designed to streamline efficiencies, cut costs, boost client engagement, and more. We can help you formulate a solid enterprise product strategy.</p>
                </a>
              </div>
              <div className="block-features__item block-features__item--link">
                <a href="startups.html">
                  <h3 className="block-features__heading">Product Strategy for Startups</h3>
                  <p className="block-features__text">A myriad of obstacles face founders on the road from idea to successful execution. Intellectsoft can help your startup foresee and deftly navigate those road bumps by digging into the details of your idea, cross-examining potential pitfalls, creating a prototype, and preparing the product for development.</p>
                </a>
              </div>
              <div className="block-features__item block-features__item--link">
                <a href="market-product-research.html">
                  <h3 className="block-features__heading">Market &amp; Product Research</h3>
                  <p className="block-features__text">Whether you are thinking about developing new solutions or are already in the thick of it—Intellectsoft can help you steer your product to a lucrative market niche.</p>
                </a>
              </div>
              <div className="block-features__item block-features__item--link">
                <a href="design-lab.html">
                  <h3 className="block-features__heading">UI/UX Design</h3>
                  <p className="block-features__text">Even the most powerful, comprehensive, and reliable software solutions can fall short of success without an intuitive design and a pleasant user experience. Intellectsoft can help you walk that extra mile.</p>
                </a>
              </div>
            </div>
          </div>
        </section>
        <Commonsections/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>
     );
   }
 }



