import React, { Component } from 'react';
import AboutYourProject from '../Services/common-Sections/AboutYourProject';
import SuccessStories from '../Services/common-Sections/SuccessStories';
import commonsections from '../Services/common-Sections/common-sections';
class appmanagement extends Component {
  render()
   {
   return (
   <div>
   <section className="header" style={{backgroundImage: 'url(' + require('../../images/platforms/d3087cea777d648fc1f6288e5e04ed1635c6454366d7e447d6d692fac9d967ac.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">Content Management</h1>
            <p className="header__lead">Update your application when it’s most convenient to you. Add news, images, videos, and even core application features immediately, without the usual app store approval waiting time.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <h3>Convenient Panel</h3>
            <p>Roll out your application’s content and feature updates from within an intuitive admin panel. Change the look, feel, and contents of your mobile application in one place, and roll out your updated application with the click of a button.</p>
            <hr />
            <h3>Remove Bottlenecks</h3>
            <p>Provide key people in your business organization with access to the panel to circumvent traditional hurdles. Delegate work, let the marketing team do what they do best, and do what your business needs without the involvement of a development team.</p>
          </div>
        </section>       

      <commonsections/>
      <SuccessStories/>
      <AboutYourProject/>
   </div>
   );
   }
   }   
export default appmanagement;



