import React, { Component } from 'react';
import AboutYourProject from '../Services/common-Sections/AboutYourProject';
import SuccessStories from '../Services/common-Sections/SuccessStories';
import commonsections from '../Services/common-Sections/common-sections';
class geolocationNavigation extends Component {
  render()
   {
   return (
   <div> 
    <section className="header" style={{backgroundImage: 'url(' + require('../../images/platforms/28a6bf43f1e6560c2b42a82e7adc88e25e6133c6c0a5b0e692aa457fccebdd44.jpg')+ ')'}}>
          <div className="container">
            <h1 className="header__title">Geolocation and Navigation</h1>
            <p className="header__lead">Outdoor/indoor navigation and geolocation targeting platforms that create a bond between your brick and mortar business or event and your mobile software.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <h3>Indoor Navigation</h3>
            <p>Simplify your client’s life by providing intuitive indoor navigation options at your office, venue, or event. Use their device’s built-in hardware to get them to where you need them to go faster and more conveniently.</p>
            <hr />
            <h3>Geolocation</h3>
            <p>Leverage the insights and benefits that are provided by geolocation. In any environment and in any relationship, both with your employees and your customers, geolocation can be used to send targeted alerts and notifications, and location data can be used to discover areas of congestion and new opportunities for growth.</p>
          </div>
        </section>   
      <commonsections/>
      <SuccessStories/>
      <AboutYourProject/>
   </div>
   );
   }
   }   
export default geolocationNavigation;



