import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
     <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/6559c069c86c92ec197df0512802fb5803425ddbcb5590dfd514f17aefa1acaa.jpg')+ ')'}}>
          <div className="container">
            <h1 className="header__title">Quality Assurance</h1>
            <p className="header__lead">Avoid future challenges by ensuring you are working with great software from the very start. Our qualified Quality Assurance and software testing team can help you be sure that the software that your business runs on works smoothly and as expected — always.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <h3>Enterprise, Mobile, and Web Testing</h3>
            <p>As a company that prides itself on developing high-quality enterprise software solutions, we take Quality Assurance seriously. Each project we take on goes through numerous iterations of testing to ensure smooth performance, user-friendliness, and bulletproof security.</p>
            <h3>Dedicated Teams</h3>
            <p>Ensure that Quality Assurance does not become the weakest link in your software development workflow. Intellectsoft can help you fill key positions in your production team with Quality Assurance specialists that have proven themselves to be exceptionally thorough and have had experience in testing enterprise solutions, mobile applications, and web applications.</p>
            <h3>Software Audit</h3>
            <p>Sometimes the only thing that’s missing is an objective outsider’s look at the software that is helping you run your business. Bring in a team of experienced Quality Assurance specialists and consultants to analyze your software and spot key problem areas and discover potential growth opportunities.</p>
            <hr />
            <h2>Testing Stages</h2>
            <p>Each project we take on goes through several or all of the following testing stages:</p>
            <p><em>Functionality Testing — We go through and check the software to ensure its front-end and back-end perform smoothly.</em></p>
            <p><em>Compatibility Testing — We ensure that the software will work on the hardware used by your employees and your customers.</em></p>
            <p><em>Performance Testing — We run the software to its limits and ensure that it can withstand the demands of your growing business.</em></p>
            <p><em>Security Testing — We perform an end-to-end security check to ensure your data is safe and your security is bulletproof.</em></p>
            <p><em>Usability Testing — We make sure the UI/UX is as intuitive and user-friendly as we promised it would be.</em></p>
          </div>
        </section>
        <CasestudySlider/>
            <Commonsections/>
        <Statistics/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>

     );
   }
 }



