import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
     <div>
        <section className="header" style={{backgroundImage: 'url(' +require('../../images/services/0d4bb948e370e76330822ed8535396951d63e5d1ea260b78d8139e0f77072bde.jpg') + ')'}}>
        <div className="container">
          <h1 className="header__title">Architecture</h1>
          <p className="header__lead">Cut costs and ensure success by creating a solid roadmap before you begin development. From your project's inception to its completion—know what to expect and understand the specifics of the work that will need to be done.</p>
        </div>
      </section>
      <section className="about">
        <div className="container">
          <p>We live by the mantra “measure twice, cut once.” By mapping software components out early, you avoid costly mistakes and ensure that software fulfills the task it was designed for. Software architecture can reduce system complexity and ensure systems are flexible and aligned with your business strategy. By supporting development teams in estimating effort, fulfilling technical requirements, and roadmapping.</p>
          <h4>Intellectsoft can respond effectively to a wide range of business needs such as:</h4>
          <ul>
            <li>Project Architecture Support, from project inception to completion, ensuring that solutions are being correctly implemented and augmenting the team if necessary.</li>
            <li>Software Architecture Design and Modeling, to prioritize the project’s architectural drivers and improve definition of IT strategies for greater predictability and reduced risk.</li>
            <li>Legacy Modernization Assessment, such as migrating to new tech or refactoring existing solutions.</li>
          </ul>
        </div>
      </section>
      <CasestudySlider/>
      <Commonsections/>
      <Statistics/>
      <SuccessStories/>
      <AboutYourProject/>
      </div>
      
     );
   }
 }



