import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';


export default class Footer extends React.Component {
  render()
   {
     return (
      <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/4e074c0e8351efb4709b34560530ca45f573e4ff2e2bec30a3b440cd029457d2.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">Big Data &amp; Analytics</h1>
            <p className="header__lead">Improve the way you collect and work with Big Data and Analytics. Create a scalable way to consistently be able to boost your business intelligence, develop a reliable way to analyze your data, and give your employees a convenient way to visualize and understand your customers’ preferences and the market’s trends.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <h3>Data Architecture</h3>
            <p>The most surefire way to save costs and maximise ROI when approaching Business Intelligence is to approach development with a clear roadmap. Intellectsoft can analyze your business needs and leverage the latest technological advances to provide your business with a data architecture model that gives your employees and decision makers precisely the information they need exactly when they need. Whether you are looking for real-time access to data or solid processes and rules for the collection of data, our consultants and developers can help you arrive at the perfect solution.</p>
            <h3>Analytics and Visualization</h3>
            <p>Your data can only be as useful as the information and analytics it is capable of providing you with. Intellectsoft can help you develop convenient reporting and visualization tools, as well as an intuitive user-interface complete with dashboards that will allow your key decision-makers to have the information they need available to them at a moment’s notice.</p>
            <h3>Data Mining and Predictive Analytics</h3>
            <p>Take your approach to Big Data a step further with data mining and predictive analytics. Automate the process of discovering new patterns in large data sets and simplify the process of extracting data in a comprehensive, understandable structure.</p>
            <h3>Consulting</h3>
            <p>When time is of the essence, opting for a third-party solution can expedite the process of collecting and analyzing the operational and reporting data available to you. Intellectsoft consultants can help you choose the right Business Intelligence suite that meets your business requirements and works smoothly within your current Enterprise Architecture landscape.</p>
            <hr />
            <h2>Our Process</h2>
            <p>Discovery &amp; Analysis — A qualified team of system-analysts and consultants observes your current solutions, discovers key problems and opportunities for improvement, and develops a roadmap based on your business needs.</p>
            <p>Development &amp; Testing — Qualified developers with niche expertise develop, test, and deliver a turnkey Big Data solution for your enterprise.</p>
            <p>Implementation &amp; Support — Intellectsoft aids you in implementing the new suite into your daily business operations and provides thorough support and maintenance of your software.</p>
            <hr />
            <h2>Technology</h2>
            <p>Relational and NoSQL databases: MySQL, PostgreSQL, Microsoft SQL Server, MongoDB and others</p>
            <p>Web Technologies and frameworks: Python, PHP, Java, .NET</p>
            <p>Modern JavaScript charting and visualization frameworks: D3.js, Chart.js</p>
            <p>DevOps and Cloud Computing expertise</p>
          </div>
        </section>
            <CasestudySlider/>
            <Commonsections/>
            <Statistics/>
            <SuccessStories/>
            <AboutYourProject/>
      </div>
  
     );
   }
 }



