import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
      <div>
         <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/918c8df6d4f0bb934f5acf7246c33f9c7d76b0a420899d1041c044e3e6b23246.jpg')+ ')'}}>
          <div className="container">
            <h1 className="header__title">Technical Program Management</h1>
            <p className="header__lead">Get ahead in your software development with a strong team-leader at the helm. Our Technical Program Management consultants are experienced, qualified, and possess the type of industry expertise that can help your business both determine and accomplish key development goals.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>Intellectsoft’s Technical Program Management consultants have at at least 10 years of experience managing large and complex technical programs. Typically, that would include deep project and program management expertise, plus strong background in one or more of the following areas:</p>
            <ul>
              <li>Software Development</li>
              <li>Solution Architecture</li>
              <li>Enterprise Architecture</li>
              <li>IT Strategy and Operations</li>
            </ul>
            <p>Many of our Technical Program Management consultants have earned their wings in both Corporate IT departments and well-known IT Consulting Firms.</p>
            <p>Our Technical Program Management consultants can help you with any or all of the following stages of the IT Program lifecycle:</p>
            <ul>
              <li>Identifying Program Objectives</li>
              <li>Defining Program Approach and Scope</li>
              <li>Establishing Program Governance</li>
              <li>Program Planning</li>
              <li>Managing Program Delivery</li>
              <li>Closing the Program and Transitioning to Service</li>
            </ul>
            <p>Intellectsoft’s IT Consulting team works hand in hand with our team of senior technology specialists. Our Technical Program Management service can be augmented with our strong expertise in <a href="enterprise-development.html">Project-based Software Development</a>, as well as <a href="staffing.html">Out-Staffing Service.</a></p>
          </div>
        </section>
       <CasestudySlider/> 
        <Commonsections/>
        <Statistics/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>
     );
   }
 }



