import React from 'react';
export default class Shell extends React.Component {
  render()
   {
   return(
   <div>
    <header data-0="background-position:0px 0px;" data-900="background-position:0px 360px;" className="page-header--full-scr page-header page-header--centered" style={{backgroundImage: 'url(' + require('../../images/cases/d399aed414d04cd440cbd460f3e224c7545e8de52fb2633d3af20902f37f6069.jpg')+ ')'}}>
        <div className="container">
          <div className="page-header__overlay" />
          <div className="page-header__content">
            <div className="page-header__client-logo shell">
            </div>
            <div className="page-title">
              <h1>Shell</h1>
            </div>
            <div className="page-lead">
              <p>Shell’s Robert Training Center gets a custom mobile app that helps its trainees, administrators, and visitors navigate the 20-acre campus and communicate crucial messages, making documentation digital and gathering it one place.</p>
            </div>
          </div>
        </div>
      </header>
       <section className="case-mask" style={{backgroundImage: 'url(' + require("../../assets/5be2fa1f/img/cases/shell-mask.jpg") + ')'}}>
        <div className="container">
          <span className="case-mask__content">
            <p>Robert Training Center (RTC) provides training and education to future Shell employees and contractors who will be working on offshore oil and gas platforms worldwide.</p>
          </span>
        </div>
      </section>
      <main className="case-fixed" role="main">
      <section id="pinned-trigger1" className="case-fixed__item">
        <div className="case-fixed__row">
          <div id="pinned-element1" className="case-fixed__fixed-img">
            <div className="img-wrap">
              <div className="img">
                <img data-item="challenge" src={require("../../assets/5be2fa1f/img/cases/sliders/shell/slide-1.jpg")} alt="Challenge" />
                <img data-item="solution" className="case-fixed__hidden-image" src={require("../../assets/5be2fa1f/img/cases/sliders/shell/slide-2.jpg")} alt="Solution" />
                <img data-item="impact" className="case-fixed__hidden-image" src={require("../../assets/5be2fa1f/img/cases/sliders/shell/slide-3.jpg")} alt="Impact" />
              </div>
            </div>
          </div>
          <div className="case-fixed__text">
            <h2>Challenge</h2>
            <div className="case-slider__slide-text">
              <p>Like many educational establishments, RTC relied on a massive amount of paperwork and printed materials spread around the training center grounds. RTC needed an all-in-one, paperless way to communicate quickly and effectively with the large number of trainees and visitors. Concurrently, the training center needed to ensure effortless navigation for anyone who set their feet on the campus grounds. The ultimate goal was to eliminate every possible impediment to the educational and training processes.</p>
            </div>
          </div>
        </div>
      </section>
       <section id="solution" className="case-fixed__item">
          <div className="case-fixed__row">
            <div className="img-wrap img-container">
              <img className="case-fixed__hidden-image case-fixed__show-small" src={require("../../assets/5be2fa1f/img/cases/sliders/shell/slide-2.jpg")} alt="Solution" />
            </div>
            <div className="case-fixed__text">
              <h2>Solution</h2>
              <div className="case-slider__slide-text">
                <p>Intellectsoft designed an app for iOS and Android that turned the RTC campus navigation from laborious to leisurely. The application uses geolocation to give students detailed directions to every classroom, emergency checkpoint, and other locations. RTC trainees can use the app to find relevant course information: timetables, instructor biographies, training descriptions, event schedules, and more.</p>
              </div>
            </div>
          </div>
        </section>
        <section id="impact" className="case-fixed__item">
          <div className="case-fixed__row">
            <div className="img-wrap img-container">
              <img className="case-fixed__hidden-image" src={require("../../assets/5be2fa1f/img/cases/sliders/shell/slide-3.jpg")} alt="Impact" />
            </div>
            <div className="case-fixed__text">
              <h2>Impact</h2>
              <div className="case-slider__slide-text">
                <p>RTC administrators no longer need to distribute class schedules, maps, and answer numerous common questions from the trainees and visitors, allowing them to focus more on their training and education. On the other hand, the trainees no longer need to consult a multitude of documents and materials for any general course-related information, and are able to comfortably navigate the campus grounds. Most notably, the app will allow the RTC administration to deliver timely push notifications with safety-related messages, schedule changes, and event information.</p>
              </div>
            </div>
          </div>
        </section>
      </main>
      </div>
   
   );
   }
   }
   



