import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
      <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/8cf1875e3a6f260b8eb5d81e7fd86f576ab8ff556cf6783344e4bb264adc23e4.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">Product Strategy for Startups</h1>
            <p className="header__lead">A myriad of obstacles face founders on the road from idea to successful execution. Intellectsoft can help your startup foresee and deftly navigate those road bumps by digging into the details of your idea, cross-examining potential pitfalls, creating a prototype, and preparing the product for development.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>We understand the difficulty of hiring a startup development team and the struggle with software development for startups, so we created our Startup Strategy Workshop studio to help your startup foresee and deftly navigate those road bumps.</p>
            <p>During this 8-10 week course, our expert startup development team and product managers work with you to dig into the details of your idea, cross-examine potential pitfalls, create a startup strategy plan, and prepare the product for development. By helping you create a clear, comprehensive strategy for your startup business, Intellectsoft will carry you quickly to the curve of the hockey stick and help you benefit from our experience in app development for startups.</p>
          </div>
        </section>
        <CasestudySlider/>
        <Commonsections/>
        <Statistics/>
        <SuccessStories/>   
        <AboutYourProject/>   
      </div>
     );
   }
 }



