import React from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import home from '././components/home';
import About from '././components/about';
import Careers from '././components/careers';
import Team from '././components/team';
import Partners from '././components/partners';
import PrivacyPolicy from '././components/privacy-policy';
import Clients from '././components/clients';
import Contacts from '././components/contacts';
import Main from '././components/main';
import Resources from '././components/resources';
import Sitemap from '././components/sitemap';
import Notfound from '././components/Notfound';


/* START IMPORT  SERVICES PAGES  */ 
import Services from '././components/Services/services';
import Appdevelopers from '././components/Services/Appdevelopers';
import architecture from '././components/Services/architecture';
import bigdataanalytics from '././components/Services/big-data-analytics';
import designlab from '././components/Services/design-lab';
import devops from '././components/Services/dev-ops';
import enterprisedevelopment from '././components/Services/enterprise-development';
import itconsultingservices from '././components/Services/it-consulting-services';
import marketproductresearch from '././components/Services/market-product-research';
import mobiledevelopment from '././components/Services/mobile-development';
import processservices from '././components/Services/process-services';
import productdevelopment from '././components/Services/product-development';
import productstrategy from '././components/Services/product-strategy';
import projectrecovery from '././components/Services/project-recovery';
import qatesting from '././components/Services/qa-testing';
import security from '././components/Services/security';
import softwaremaintenancesupport from '././components/Services/software-maintenance-support';
import staffing from '././components/Services/staffing';
import startups from '././components/Services/startups';
import teamextension from '././components/Services/team-extension';
import technicalprogrammanagement from '././components/Services/technical-program-management';
import technologicalservices from '././components/Services/technological-services';


/* END IMPORT  SERVICES PAGES  */ 

/* START IMPORT  BLOG PAGES  */ 
import blog from '././components/Blog/blog';  
         
import mobileenterpriseservicesconsideravoid from '././components/Blog/3-mobile-enterprise-services-consider-2017-1-avoid';
import playmakerfeaturesbrandedteamapp from '././components/Blog/3-playmaker-features-branded-team-app';
import waysfintechsolutionsapplymobileapps from '././components/Blog/3-ways-fintech-solutions-apply-mobile-apps';
import benefitsstaffaugmentation from '././components/Blog/5-benefits-staff-augmentation';
import talksyoudontwanttomissatcollision2016 from '././components/Blog/7-talks-you-dont-want-to-miss-at-collision-2016';
import appseverybaseballfanshouldhave from '././components/Blog/10-apps-every-baseball-fan-should-have';
import appledesignawardwinners from '././components/Blog/2015-apple-design-award-winners';
import absoluteguidefindingappdevelopers1 from '././components/Blog/absolute-guide-finding-app-developers-p-1';
/* END IMPORT BLOG PAGES  */ 

 import cases from '././components/cases/cases'; 
 import Alumties from '././components/cases/alumties'; 
 import Shell from '././components/cases/shell'; 
 import Docs from '././components/Docs/docs'; 
 import blockchaintechnologyguide from '././components/Docs/blockchain-technology-guide'; 
 import blockchainwebinar from '././components/Docs/blockchain-webinar';
  
 import kadosolutionforar from '././components/l/17/kado-solution-for-ar'; 
 import Solutions from '././components/solutions/solutions'; 
 import Fintech from '././components/solutions/fintech'; 
 import internetofthings from '././components/solutions/internet-of-things'; 
 import Technologies from '././components/technologies/technologies'; 
 import androidapplicationdevelopment from '././components/technologies/android-application-development'; 
 
  import Platforms from '././components/platforms/platforms'; 
  import Advertising from '././components/platforms/advertising'; 
  import sportsapps from '././components/platforms/sports-apps'; 
  import geolocationnavigation from '././components/platforms/geolocation-navigation'; 
  import appmanagement from '././components/platforms/app-management'; 

const Routes = () => (
  <BrowserRouter >
	<Switch> 
	
	     <Route exact path="/" component={home}/> 
	     <Route  path="/about" component={About}/>
         <Route  path="/careers" component={Careers}/>
         <Route  path="/team" component={Team}/>
         <Route  path="/partners" component={Partners}/>
         <Route  path="/privacy-policy" component={PrivacyPolicy}/>
         <Route  path="/clients" component={Clients}/>
         <Route  path="/contacts" component={Contacts}/>
         <Route  path="/main" component={Main}/>
         <Route  path="/resources" component={Resources}/>
         <Route  path="/sitemap" component={Sitemap}/>
       
          {/* START  BLOG PAGES ROUTE */         }
          
          <Route exact path="/blog" component={blog}/>
          <Route  path="/blog/3-mobile-enterprise-services-consider-2017-1-avoid" component={mobileenterpriseservicesconsideravoid}/>
          <Route  path="/blog/3-playmaker-features-branded-team-app" component={playmakerfeaturesbrandedteamapp}/>
          <Route  path="/blog/3-ways-fintech-solutions-apply-mobile-apps" component={waysfintechsolutionsapplymobileapps}/>
          <Route  path="/blog/5-benefits-staff-augmentation" component={benefitsstaffaugmentation}/>
          <Route  path="/blog/7-talks-you-dont-want-to-miss-at-collision-2016" component={talksyoudontwanttomissatcollision2016}/>
          <Route  path="/blog/10-apps-every-baseball-fan-should-have" component={appseverybaseballfanshouldhave}/>
          <Route  path="/blog/2015-apple-design-award-winners" component={appledesignawardwinners}/>
          <Route  path="/blog/absolute-guide-finding-app-developers-p-1" component={absoluteguidefindingappdevelopers1}/>  

        {  /* END  BLOG PAGES ROUTE */}
          
         { /* START  SERVICES PAGES ROUTE */ }
          
          <Route exact path="/services" component={Services}/>
          <Route  path="/services/app-developers" component={Appdevelopers}/>
          <Route  path="/services/architecture" component={architecture}/>
          <Route  path="/services/big-data-analytics" component={bigdataanalytics}/>
          <Route  path="/services/design-lab" component={designlab}/>
          <Route  path="/services/dev-ops" component={devops}/>
          <Route  path="/services/enterprise-development" component={enterprisedevelopment}/>
          <Route  path="/services/it-consulting-services" component={itconsultingservices}/>
          <Route  path="/services/market-product-research" component={marketproductresearch}/>
          <Route  path="/services/mobile-development" component={mobiledevelopment}/>
          <Route  path="/services/process-services" component={processservices}/>
          <Route  path="/services/product-development" component={productdevelopment}/>
          <Route  path="/services/product-strategy" component={productstrategy}/>
          <Route  path="/services/project-recovery" component={projectrecovery}/>
          <Route  path="/services/qa-testing" component={qatesting}/>
          <Route  path="/services/security" component={security}/>
          <Route  path="/services/software-maintenance-support" component={softwaremaintenancesupport}/>
          <Route  path="/services/staffing" component={staffing}/>
          <Route  path="/services/startups" component={startups}/>
          <Route  path="/services/team-extension" component={teamextension}/>
          <Route  path="/services/technical-program-management" component={technicalprogrammanagement}/>
          <Route  path="/services/technological-services" component={technologicalservices}/>
          
        {  /* END SERVICES PAGES ROUTE */}
          
       { /* START  CASES PAGES ROUTE */}
                
           <Route exact path="/cases" component={cases}/>
           <Route  path="/cases/alumties" component={Alumties}/>
           <Route  path="/cases/shell" component={Shell}/>          
           
       { /* END  CASES PAGES ROUTE */ }
           
        { /* START  DOCS PAGES ROUTE */ }
            
           <Route exact path="/docs" component={Docs}/>        
           <Route  path="/docs/blockchain-technology-guide" component={blockchaintechnologyguide}/>
           <Route  path="/docs/blockchain-webinar" component={blockchainwebinar}/>                   
       { /* END  DOCS PAGES ROUTE */ }
        
          <Route  path="/l/17/kado-solution-for-ar" component={kadosolutionforar}/>
          
          <Route exact path="/solutions" component={Solutions}/> 
          <Route  path="/solutions/fintech" component={Fintech}/> 
          <Route  path="/solutions/internet-of-things" component={internetofthings}/> 
                            
          <Route exact path="/technologies" component={Technologies}/>   
          <Route  path="/technologies/android-application-development" component={androidapplicationdevelopment}/>   
                          
          <Route exact path="/platforms/" component={Platforms}/>
          <Route  path="/platforms/advertising" component={Advertising}/>
          <Route  path="/platforms/sports-apps" component={sportsapps}/>
          <Route  path="/platforms/geolocation-navigation" component={geolocationnavigation}/>
          <Route  path="/platforms/app-management" component={appmanagement}/>
          <Route  path="/*" component={Notfound}/>
          
        
         
         
    
      
	</Switch>
  </BrowserRouter>

);
export default Routes;




