import React from 'react';
import CasestudySlider from './common-Sections/ServicesCasestudySlider';
import Commonsections from './common-Sections/common-sections';
import Statistics from './common-Sections/Statistics';
import SuccessStories from './common-Sections/SuccessStories';
import AboutYourProject from './common-Sections/AboutYourProject';
export default class Footer extends React.Component {
  render()
   {
     return (
        <div>
        <section className="header" style={{backgroundImage: 'url(' + require('../../images/services/795d54cbfb6a1ec3129894e5e419ce149b5b1b0d0c93130bad5f9ec482a6b255.jpg') + ')'}}>
          <div className="container">
            <h1 className="header__title">Project Recovery Services</h1>
            <p className="header__lead">There is no reason you have to go down with your ship. The qualified consulting, development, and maintenance teams at Intellectsoft can take your failing project back to the top of the game. We will understand the problems you are facing and provide you with precisely the kind of support you will need to turn everything around and start making confident steps towards flawless project execution.</p>
          </div>
        </section>
        <section className="about">
          <div className="container">
            <p>Intellectsoft provides critical recovery services to get failing projects back on track and the process is usually the same:</p>
            <p><strong>Understand</strong> - we will interview the client’s leadership and project team and review key project documents to understand the project objectives, business goals, as well as the key project constraints (scope, budget, and resources)</p>
            <p><strong>Review and Analyze</strong> - we will take a close look at the project, review project plans, status reports and other documents, and talk to the team. Our technology experts will perform architecture and code reviews, and our methodology specialists will examine your SDLC approach. Typically, the following areas of the project should be looked at:</p>
            <ul>
              <li><strong>Management</strong> Governance, risk mgmt, planning, status monitoring, communications</li>
              <li><strong>Process</strong> SDLC Methodology, DevOps etc.</li>
              <li><strong>Technology</strong> Solution architecture, tech. stack, integration with other systems</li>
              <li><strong>Improving</strong> project communications and stakeholder management</li>
              <li><strong>Quality</strong> QA/QC, Performance, Scalability, Security</li>
            </ul>
            <p><strong>Plan</strong> - Once the project review is complete, our team will put together a comprehensive project remediation plan. This plan would include a set of specific recommendations for bringing the project back on track in the areas of governance, communication, process, technology and architecture, and quality. In some cases, the appropriate course of action would be to redefine or close the project.</p>
            <p><strong>Remediate</strong> - this phase will be focused on implementing remediation plan. Our team will closely manage remediation process and provide clear updates to the client’s IT and business leadership throughout.</p>
            <p><strong>Transition</strong> - When the remediation effort is complete, we will ensure that the project is transitioned to our customer’s project leadership team. If the project is completed or retired, we will also ensure that the project is formally closed.</p>
            <p><strong>Staff Augmentation</strong> - At any stage of the project, Intellectsoft Staff Augmentation services would be help your project, from providing technical and PM experts to address project issues, to providing fully staffed dedicated development teams</p>
            <p><strong>Mentor</strong> - Provide mentoring to client’s team throughout the recovery process. Even the most technologically competent organizations occasionally run into issues with project execution. Intellectsoft is here to help you get things back on track.</p>
          </div>
        </section>
        <CasestudySlider/>
        <Commonsections/>
        <Statistics/>
        <SuccessStories/>
        <AboutYourProject/>
      </div>

     );
   }
 }



