import React from 'react';
export default class NotFound extends React.Component {
  render()
   {
   return (
   <section className="error-page">
        <div className="container">
          <div className="error-page__content">
            <p className="error-page__code">404</p>
            <p className="error-page__text">Page not found.</p>
            <p className="error-page__message">The above error occurred while the Web server was processing your request.</p>
            <a className="btn" href="/">Go to home page</a>			</div>
        </div>
      </section>
   );
   }
   }



